<template>
    <div class="buy">
      <header>
        <van-icon name="arrow-left" size="20" @click="$router.back()" />
        <div class="title">我的订单</div>
      </header>
      <!-- 产品 -->
      <div class="product" v-for="(o, index) in filterObj" :key="index">
        <van-swipe-cell v-for="(item, index) in o" :key="index">
          <van-card
            :price="item.price"
            :title="item.name"
            class="goods-card"
            :thumb="item.img_url"
            :num="item.count ||  order[index].count"
            @click="
              $router.push({
                path: '/productdetails',
                query: {
                  product_id: item.product_id,
                },
              })
            "
          />
        </van-swipe-cell>
        <div class="serve">
          <div>配送服务</div>
          <div>
            <div>快递 免邮</div>
            <div>预计24小时内发货</div>
          </div>
        </div>
        <div class="serve">
          <div>订单编号</div>
          <div>
            <span>{{ order[index].id }} |</span> 复制
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { mapState } from "vuex";
  
  export default {
    computed: {
      ...mapState(["orderList", "token"]),
      order() {
        return this.orderList.filter(
          (o) => o.state === "已完成" && this.token == o.token
        );
      },
      // 过滤不是对象
      filterObj() {
        return Object.values(this.order).map((item) => {
          return Object.values(item).filter((item) => {
            if (typeof item === "object") {
              return item;
            }
          });
        });
      },
    },
  };
  </script>
  
  <style lang="less" scoped>
  .buy {
    background: #f2f2f2;
    height: 100vh;
    header {
      margin-top: 0px;
      color: rgba(0, 0, 0, 0.8);
      font-size: 16px;
      font-weight: 600;
      text-align: center;
      padding: 0.3rem 0;
      background: #fff;
      display: flex;
      justify-content: center;
      .title {
        width: 90vw;
        text-align: center;
      }
    }
    .product {
      margin: 0.2rem auto;
      width: 96vw;
      border-radius: 10px;
      background: #fff;
      padding-bottom: 0.2rem;
      .serve {
        width: 94%;
        font-size: 14px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.2rem 0;
        margin: auto;
        span {
          color: #000;
          opacity: 0.5;
        }
      }
      .btn {
        width: 94%;
        font-size: 14px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.2rem 0;
        margin: auto;
        color: rgb(253, 81, 1);
      }
    }
  }
  .van-card {
    border-radius: 10px;
    background: #fff;
  }
  </style>